import React, {useState} from 'react';
import s from "./Header.module.css";
import {NavLink} from "react-router-dom";


const ButtonBlock = () => {

    const [selectToggle, setSelectToggle] = useState<boolean>(false)

    const onMouseOverHandler =()=>{
        setSelectToggle(true)
    }

    const onMouseOutHandler =()=>{
        setSelectToggle(false)
    }

    return (
        <div className={s.buttonBlock}>
            <div className={s.button}><NavLink to={'/main'}></NavLink></div>
            <div className={s.button}><NavLink to={'/portfolio'}></NavLink></div>
            <div className={s.button}></div>
            <div onMouseOut={onMouseOutHandler} onMouseOver={onMouseOverHandler} className={s.button}>

                {selectToggle &&
                <div className={s.select}>
                    <div className={s.selectItem}></div>
                    <div className={s.selectItem}></div>
                    <div className={s.selectItem}> </div>
                </div>}
            </div>
        </div>
    );
};

export default ButtonBlock;